const historia = `La Comuna Centro del Pueblo Alangasí, fundada en el seno de la cultura milenaria andina, tiene como misión fundamental cultivar la tierra sagrada del Ilaló para garantizar la seguridad alimentaria de la población de Alangasí. Esta labor, que se ha transmitido de generación en generación, representa un compromiso inquebrantable con la Pachamama, la comunidad y las tradiciones ancestrales.
Honramos las técnicas agrícolas tradicionales, adaptadas a las características únicas del Ilaló, que han permitido a la comuna prosperar durante siglos.
Promovemos la agricultura sostenible que protege la rica biodiversidad del Ilaló, incluyendo la flora endémica y las especies animales que la habitan.
El cultivo del Ilaló es un pilar fundamental de la identidad cultural de la comuna, transmitiendo valores como la reciprocidad, el trabajo comunitario y el respeto por la naturaleza.
`
const mision = `La Comuna San Pedro del Tingo, arraigada en las ancestrales tradiciones andinas, ha abrazado una misión vital: salvaguardar y cultivar la tierra sagrada que cobija a Alangasí. Esta noble tarea, heredada de generación en generación, es un juramento indeleble hacia la Pachamama, la comunidad y las raíces culturales.
Nos enorgullecemos de nuestras técnicas agrícolas tradicionales, adaptadas con sabiduría a las singulares características del entorno del Ilaló, que han nutrido a nuestra comuna a lo largo de los siglos.
Fomentamos una agricultura sostenible que respeta y protege la exuberante biodiversidad del Ilaló, incluyendo su flora endémica y las especies animales que en ella habitan.
El cultivo del Ilaló es el núcleo de nuestra identidad cultural, transmitiendo valores de reciprocidad, trabajo en comunidad y reverencia por la naturaleza.
Nuestro compromiso con la tierra es un lazo que une a nuestra comunidad, fortaleciendo nuestra conexión con la tierra que nos sustenta y nutre nuestra esencia como pueblo.
`;
